import React, { useState } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import { styled } from "@mui/system";

const data = [
  { id: 1, name: " ", rank: "", time: "9:05", status: "" },
  { id: 2, name: " ", rank: "", time: "9:08", status: "" },
  { id: 3, name: " ", rank: "I", time: "9:10", status: "" },
  { id: 4, name: " ", rank: "I", time: "9:13", status: "" },
  { id: 5, name: " ", rank: "I", time: "9:17", status: "" },
  { id: 6, name: " ", rank: "I", time: "9:21", status: "" },
  { id: 7, name: " ", rank: "I", time: "9:25", status: "" },
  { id: 8, name: " ", rank: "I", time: "9:26", status: "" },
  { id: 9, name: " ", rank: "I", time: "9:30", status: "" },
  { id: 10, name: " ", rank: "I", time: "9:32", status: "" },
  { id: 11, name: " ", rank: "I", time: "9:35", status: "" },
  { id: 12, name: " ", rank: "I", time: "9:38", status: "" },
  { id: 13, name: " ", rank: "I", time: "9:42", status: "" },
  { id: 14, name: " ", rank: "I", time: "9:45", status: "" },
  { id: 15, name: " ", rank: "I", time: "9:50", status: "" },
  { id: 16, name: " ", rank: "I", time: "9:52", status: "" },
  { id: 17, name: " ", rank: "I", time: "9:55", status: "" },
  { id: 18, name: " ", rank: "II", time: "10:00", status: "" },
  { id: 19, name: " ", rank: "II", time: "10:05", status: "" },
  { id: 20, name: " ", rank: "II", time: "10:10", status: "" },
  { id: 21, name: " ", rank: "II", time: "10:15", status: "" },
  { id: 22, name: " ", rank: "II", time: "10:20", status: "" },
  { id: 23, name: " ", rank: "II", time: "10:30", status: "" },
  { id: 24, name: " ", rank: "I ", time: "10:35", status: "" },
];

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  fontSize: "14px",
  fontWeight: "bold",
}));

export default function ScheduleTable() {
    const [rows, setRows] = useState(data);

    const handleStatusChange = (id: number, newStatus: string) => {
      const updatedRows = rows.map((row) =>
        row.id === id ? { ...row, status: newStatus } : row
      );
      setRows(updatedRows);
    };
  
    const getRowColor = (status: string) => {
      switch (status) {
        case "  ":
          return "#FFA500"; // 
        case "  ":
          return "#FFD700"; // 
        case "":
          return "#32CD32"; // 
        case " ":
          return "#808080"; // 
        default:
          return "white"; // 
      }
    };

  return (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow style={{ backgroundColor: "#B8E2F2" }}>
            <StyledTableCell> /</StyledTableCell>
            <StyledTableCell>, '</StyledTableCell>
            <StyledTableCell></StyledTableCell>
            <StyledTableCell></StyledTableCell>
            <StyledTableCell></StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
        {rows.map((row) => (
            <TableRow
              key={row.id}
              style={{ backgroundColor: getRowColor(row.status) }}
            >
              <StyledTableCell>{row.id}</StyledTableCell>
              <StyledTableCell>{row.name}</StyledTableCell>
              <StyledTableCell>{row.rank}</StyledTableCell>
              <StyledTableCell>{row.time}</StyledTableCell>
              <StyledTableCell>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    value={row.status}
                    onChange={(e) => handleStatusChange(row.id, e.target.value)}
                    label=""
                  >
                    <MenuItem value=""></MenuItem>
                    <MenuItem value="  ">  </MenuItem>
                    <MenuItem value="  ">  </MenuItem>
                    <MenuItem value=""></MenuItem>
                    <MenuItem value=" "> </MenuItem>
                  </Select>
                </FormControl>
              </StyledTableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
